import {useState} from 'react';
import Layout from '../../../components/Layout';
import Sidebar from '../sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';


const Report = () => {
  const data = [
    {
      name: 'Bitcoin BTC',
      sales: 2,
      refunds: 1,
      total: 1
    },
    {
      name: 'Ethereum ETH',
      sales: 2,
      refunds: 0,
      total: 2
    },
    {
      name: 'Binance Coin BNB',
      sales: 2.5,
      refunds: 1,
      total: 1.5
    },
    {
      name: 'Ripple XRP',
      sales: 2,
      refunds: 1,
      total: 1
    },
    {
      name: 'Tether USDT',
      sales: 10,
      refunds: 1,
      total: 9
    },
    {
      name: 'CryptoPOS CPOS',
      sales: 0,
      refunds: 0,
      total: 0
    }
  ]

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-62px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome}/>
                <p className='ml-2 font-bold'>Report</p>
              </div>
              <div className='w-[50%] flex justify-between px-[2rem] mt-[1rem] text-gray-100 text-xl mb-2 italic'>
                <div>Date From: {'01/10/2022'}</div>
                <div>Date To: {'31/10/2022'}</div>
              </div>
              <div className='bg-[#333333] text-md divide-y divide-gray-400 py-1'>
                <div className='w-full flex text-gray-100 leading-[1.8rem]'>
                  <p className='w-[55%] pl-6'>Cryptocurrency</p>
                  <p className='w-[15%] text-center'>Sales</p>
                  <p className='w-[15%] text-center'>Refund</p>
                  <p className='w-[15%] text-center'>Total</p>
                </div>
                <div>
                  {
                    data.map((item, index) => {
                      return (
                        <div key={index} className='w-full flex text-gray-100 leading-[1.8rem]'>
                          <p className='w-[55%] pl-6'>{item.name}</p>
                          <p className='w-[15%] text-center'>{item.sales}</p>
                          <p className='w-[15%] text-center'>{item.refunds}</p>
                          <p className='w-[15%] text-center'>{item.total}</p>
                        </div>
                      )
                    })
                  }
                </div>
              </div>
            </div>
            <Sidebar />
        </div>
    </Layout>
  );
}

export default Report;